  font-display: swap;
  src: local("Poppins"), url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Poppins Medium"), url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Poppins Bold"), url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff"); }

/*Цвета*/
a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.no-scroll {
  height: 100vh;
  overflow: hidden; }

.page-body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #1d1613;
  scroll-behavior: smooth; }

.page-header__wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 30px;
  text-align: center; }
  @media (max-width: 1200px) {
    .page-header__wrapper {
      width: 100%;
      padding-left: 15px;
      padding-right: 15px;
      box-sizing: border-box; } }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      padding-left: 45px;
      padding-right: 45px; } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      padding: 20px 15px 0; } }
  .page-header__wrapper--active {
    height: 100vh;
    overflow-y: scroll;
    background-color: #a87b62; }

.page-header__wrapper svg {
  stroke: #1d1613; }

.page-header__wrapper--active svg,
.page-header__wrapper--active .page-header__logo svg {
  stroke: #ffffff; }

.page-header__wrapper .page-header__nav-toggle:hover svg,
.page-header__wrapper .page-header__nav-toggle:focus svg,
.page-header__wrapper .page-header__link:hover svg,
.page-header__wrapper .page-header__link:focus svg {
  stroke: #a87b62; }

.page-header__wrapper .page-header__nav-toggle:active svg,
.page-header__wrapper .page-header__link:active svg {
  stroke: #9b9997; }

.page-header__wrapper--active .page-header__nav-toggle:hover svg,
.page-header__wrapper--active .page-header__nav-toggle:focus svg,
.page-header__wrapper--active .page-header__link:hover svg,
.page-header__wrapper--active .page-header__link:focus svg {
  stroke: #1d1613; }

.page-header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 14px; }
  @media (max-width: 1023px) {
    .page-header__top {
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 20px; } }
  @media (max-width: 767px) {
    .page-header__top {
      margin-bottom: 10px; } }

@media (max-width: 1023px) {
  .page-header__top form {
    display: none;
    position: relative;
    order: 1;
    width: 100%;
    padding-top: 30px; } }

@media (max-width: 767px) {
  .page-header__top form {
    padding-top: 24px; } }

.page-header__top label {
  cursor: pointer; }
  .page-header__top label svg {
    fill: #1d1613;
    stroke: none; }
  @media (max-width: 1023px) {
    .page-header__top label {
      position: absolute;
      bottom: 8px;
      left: 12px; }
      .page-header__top label svg {
        width: 16px;
        height: 16px;
        fill: #ffffff; } }

.page-header__top input {
  border: none;
  margin-top: 6px;
  padding: 10px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613; }
  .page-header__top input::placeholder {
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #9b9997; }
  .page-header__top input:focus {
    outline: none;
    border-bottom: 1px solid #a87b62; }
  @media (max-width: 1023px) {
    .page-header__top input {
      width: 100%;
      margin-left: 0;
      padding-left: 40px;
      background: rgba(255, 255, 255, 0.15);
      border-radius: 21px; }
      .page-header__top input::placeholder {
        visibility: hidden; }
      .page-header__top input:focus {
        border-bottom: none; } }
  @media (max-width: 767px) {
    .page-header__top input {
      margin-top: 0; } }

.page-header__nav-toggle {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__nav-toggle {
      display: block;
      width: 25px;
      height: 25px;
      padding: 0;
      background-color: transparent;
      cursor: pointer;
      border: none; } }

.page-header__logo {
  padding-right: 75px; }
  @media (max-width: 1023px) {
    .page-header__logo {
      padding-right: 7px; } }

.page-header__logo svg {
  stroke: #a87b62; }
  @media (max-width: 767px) {
    .page-header__logo svg {
      width: 52px;
      height: 52px; } }

.page-header__user-nav {
  display: flex;
  padding-top: 11px; }
  @media (max-width: 1023px) {
    .page-header__user-nav {
      padding-top: 0; } }

.page-header__link {
  font-weight: 500;
  font-size: 14px;
  color: #1d1613;
  margin-right: 35px; }
  .page-header__link:last-child {
    margin-right: 0; }
  .page-header__link:hover {
    color: #a87b62; }
  .page-header__link:active {
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .page-header__link--login {
      display: none; } }

@media (max-width: 1023px) {
  .page-header__link span {
    display: none; } }

.page-header__link svg {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__link svg {
      display: block; } }

@media (max-width: 1023px) {
  .page-header__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start; } }

.page-header__nav-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  padding-left: 10px;
  list-style: none; }
  .page-header__nav-list::before {
    position: absolute;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: #f6f5f4; }
    @media (max-width: 1023px) {
      .page-header__nav-list::before {
        display: none; } }
  @media (max-width: 1023px) {
    .page-header__nav-list {
      width: 100%;
      flex-direction: column;
      align-items: flex-start;
      padding-left: 0;
      padding-top: 15px;
      margin-bottom: 20px;
      box-sizing: border-box; } }
  @media (max-width: 767px) {
    .page-header__nav-list {
      margin-bottom: 10px; } }

.page-header__nav-item {
  padding: 20px 0 18px;
  margin-right: 35px; }
  .page-header__nav-item:last-child {
    margin-right: 0; }
  @media (max-width: 1023px) {
    .page-header__nav-item {
      padding: 17px 0; } }

.page-header__nav-link {
  font-weight: 500;
  color: #1d1613; }
  .page-header__nav-link:hover {
    color: #a87b62; }
  .page-header__nav-link:active {
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .page-header__nav-link {
      font-size: 24px;
      color: #ffffff; }
      .page-header__nav-link:hover {
        color: #9b9997; }
      .page-header__nav-link:active {
        opacity: 0.7; } }

.page-header__tablet-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  margin-bottom: 25px;
  padding: 0;
  list-style: none; }
  @media (max-width: 767px) {
    .page-header__tablet-list {
      margin-bottom: 15px; } }
  @media (min-width: 1023px) {
    .page-header__tablet-list {
      display: none; } }

.page-header__tablet-item {
  padding: 10px 0; }

.page-header__tablet-item a {
  font-weight: 500;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.75); }
  .page-header__tablet-item a:hover {
    color: #9b9997; }
  .page-header__tablet-item a:active {
    opacity: 0.5; }

.page-header__tablet-login {
  margin-bottom: 65px;
  padding: 15px 0;
  font-weight: 500;
  font-size: 18px;
  color: #ffffff; }
  .page-header__tablet-login:hover {
    color: #9b9997; }
  .page-header__tablet-login:active {
    opacity: 0.7; }
  @media (min-width: 1023px) {
    .page-header__tablet-login {
      display: none; } }

@media (max-width: 1024px) {
  .page-header__nav--closed .page-header__nav-list,
  .page-header__nav--closed .page-header__tablet-list,
  .page-header__nav--closed .page-header__tablet-login {
    display: none; }
  .page-header__wrapper--active form {
    display: block; } }
